import React, { Component } from "react";
import CourseButton from "../../components/CourseButton";
import Footer from "../../components/Footer";
import Nav from "../../components/Nav";
import { Jumbotron } from "reactstrap";
import "./styles.css";

class Main extends Component {
  state = {};

  categoryNames = {
    _c1_: "Level 1",
    _c2_: "Level 2",
    _c3_: "Level 3"
  };

  componentWillMount() {
    console.log("will mount");
  }

  componentDidMount() {
    console.log("did mount");
    this._getCourses();
  }

  _getCourses = async () => {
    // async     .
    const courses = await this._callApi(); // await: this._callApi()   . this._callApi  movies  .
    this.setState({ courses });
  };

  _callApi = () => {
    return fetch("https://programmingo-korea.firebaseio.com/questionList/.json") // fetch() ajax Request...  API . fetch:  
      .then(response => response.json()) // Promise  . Promise.json()  json  
      .then(json => {
        return json;
      }) // json 
      .catch(err => console.log(err)); //     
  };

  _renderCourses = () => {
    let coursesKeys = null;
    if (this.state.courses) {
      coursesKeys = Object.keys(this.state.courses);
      return coursesKeys.map(courseKey => {
        return (
          <CourseButton
            keyName={courseKey}
            name={this.categoryNames[courseKey]}
          />
        );
      });
    }
  };

  render() {
    console.log("did render");
    const { courses } = this.state;
    return <div>
        <Nav />
        <Jumbotron className="course_wrapper">
          {this._renderCourses()}
        </Jumbotron>
        <Footer />
      </div>;
  }
}

export default Main;



